import React from "react";
import Logo from "../../Logo/Logo";
import NavigationItems from "../NavigationItems/NavigationItems";
import Styles from "./Sidedrawer.module.css";
import Hamburger from "./Hamburger/Hamburger";
import Backdrop from "../../Backdrop/Backdrop";
import Title from "../../Title/Title";

class Sidedrawer extends React.Component {
  state = {
    open: false,
  };

  SidedrawerToggle = () => {
    let open = this.state.open;
    open = !open;
    this.setState({ open });
  };
  render() {
    let backdrop = this.state.open ? (
      <Backdrop onClick={this.SidedrawerToggle} />
    ) : null;
    return (
      <>
        <div
          className={[
            Styles.Sidedrawer,
            this.state.open ? Styles.Open : Styles.Close,
          ].join(" ")}
        >
          <div className={Styles.Menu} onClick={this.SidedrawerToggle}>
            <Hamburger open={this.state.open} />
          </div>
          <div className={Styles.Logo}>
            <Logo />
          </div>
          <div className={Styles.Title}>
            <Title />
          </div>
          <nav>
            <NavigationItems onclick={this.SidedrawerToggle} />
          </nav>
        </div>
        {backdrop}
      </>
    );
  }
}

export default Sidedrawer;
