import React from "react";
import Participant from "./Participant/Participant";
import NotFound from "../NotFound/NotFound";
import AddAll from "./AddAll/AddAll";
import DeletePopup from "./DeletePopup/DeletePopup";
import styles from "./Participants.module.css";
import Button from "../Button/Button";
import { CSSTransition } from "react-transition-group";

class Participants extends React.Component {
  state = {
    participants: {
      CS19101: [
        { name: "Aditya Agrawal", roll: "19CH10004" },
        { name: "Shikha Kumari", roll: "19CE10062" },
        { name: "Mayank Agarwal", roll: "19CE30010" },
        { name: "Muskan Jain", roll: "19CH10024" },
        { name: "Samar Pratap Singh", roll: "19CH10044" },
        { name: "Utkarsh Singh", roll: "19CH10064" },
      ],
      CS29003: [
        { name: "Raghav Sharma", roll: "19AG30024" },
        { name: "Ashish Gupta", roll: "19IE10010" },
        { name: "Khushboo Bhoy", roll: "19EE30011" },
        { name: "Ritika Lath", roll: "19EC10054" },
        { name: "Tushar Mahawar", roll: "19EE10061" },
        { name: "Mayank Agrawal", roll: "19EE30014" },
      ],
      CY19001: [
        { name: "Shyamal Raj", roll: "19CE10064" },
        { name: "Mohammad Ayan Akhtar", roll: "19CE30012" },
        { name: "Aditya Chawla", roll: "19CH10006" },
        { name: "Nidhi Raj", roll: "19CH10026" },
        { name: "Sanskar Patni", roll: "19CH10046" },
        { name: "Abhishek Kumar", roll: "19CH30001" },
      ],
      PH19001: [
        { name: "Aakriti Gupta", roll: "19BT10001" },
        { name: "Ritwik Jain", roll: "19BT10021" },
        { name: "Korra Akhil", roll: "19BT30012" },
        { name: "Akshat Sharma", roll: "19CE10002" },
        { name: "Bipin Kumar Gupta", roll: "19CE10022" },
        { name: "Murali Manohar", roll: "19CE10042" },
      ],
      EE19001: [
        { name: "Sarthak Goyal", roll: "19EC30039" },
        { name: "Sudil Nain", roll: "19EC30046" },
        { name: "Keshav Sharma", roll: "19EE10034" },
        { name: "Kushagra Chitkara", roll: "19EE10036" },
        { name: "Nikhil Balwani", roll: "19EC30027" },
        { name: "Utkarsh Singh Chauhan", roll: "19GG20040" },
      ],
    },
    subcode: this.props.match.params.subcode,
    todelete: null,
    showtodelete: false,
  };

  DeleteParticipant = (roll) => {
    let participants = { ...this.state.participants };
    let index = participants[this.state.subcode].findIndex(
      (p) => p.roll === roll
    );
    participants[this.state.subcode].splice(index, 1);
    this.setState({ participants, showtodelete: false });
  };

  AddAllHandler = () => {
    alert("All Participants successfully added");
  };

  ShowDeletePopup = (participant) => {
    let todelete = participant;
    this.setState({ todelete, showtodelete: true });
  };

  render() {
    let participants = null;
    if (this.state.participants[this.state.subcode]) {
      participants = this.state.participants[
        this.state.subcode
      ].map((props, index) => (
        <Participant
          index={index + 1}
          name={props.name}
          roll={props.roll}
          key={index + 1}
          DeleteHandler={() => this.ShowDeletePopup(props)}
        />
      ));
    }

    if (participants) {
      return (
        <>
          <div className={styles.Participants}>
            <div className={styles.container}>
              <Participant index="S.No" name="Name" roll="Roll No." />
              {participants}
            </div>
            <AddAll clicked={this.AddAllHandler} />
            <Button
              secondary
              onClick={() => {
                this.props.history.goBack();
              }}
            >
              <strong>&larr; </strong>Back to Subjects
            </Button>
          </div>
          <CSSTransition
            in={this.state.showtodelete}
            timeout={1000}
            classNames={{
              enter: styles.DeletePopupEnter,
              enterActive: styles.DeletePopupEnterActive,
              exit: styles.DeletePopupExit,
              exitActive: styles.DeletePopupExitActive,
            }}
            unmountOnExit
            mountOnEnter
            onExited={() => {
              this.setState({ todelete: null });
            }}
          >
            <div className={styles.DeletePopupContainer}>
              <DeletePopup
                participant={this.state.todelete}
                closeHandler={() => {
                  this.setState({ showtodelete: false });
                }}
                continueHandler={() =>
                  this.DeleteParticipant(this.state.todelete.roll)
                }
              />
            </div>
          </CSSTransition>
        </>
      );
    } else return <NotFound />;
  }
}

export default Participants;
