import React from "react";

import Subject from "./Subject/Subject";
import styles from "./Subjects.module.css";

const subjects = [
  {
    name: "Programming and Data Structures Labaratory",
    code: "CS19101",
    credits: 3,
    semester: 1,
  },
  {
    name: "Electrical Technology Laboratory",
    code: "EE19001",
    credits: 2,
    semester: 2,
  },
  { name: "Physics-I Laboratory", code: "PH19001", credits: 2, semester: 1 },
  { name: "Algorithms Labaratory", code: "CS29003", credits: 2, semester: 3 },
  {
    name: "Chemistry Laboratory for Ist year",
    code: "CY19001",
    credits: 2,
    semester: 2,
  },
];

const Subjects = (props) => {
  const clickHandler = (subcode) => {
    props.history.push(`/subjects/${subcode}`);
  };

  return (
    <div className={styles.Subjects}>
      {subjects.map((subject) => (
        <Subject
          key={subject.code}
          name={subject.name}
          code={subject.code}
          credits={subject.credits}
          semester={subject.semester}
          clickHandler={() => clickHandler(subject.code)}
        />
      ))}
    </div>
  );
};

export default Subjects;
