import React from "react";

import styles from "./Subject.module.css";

const Subject = (props) => (
  <div className={styles.Subject} onClick={props.clickHandler}>
    <div className={styles.image}>
      <img src={"images/Subjects/" + props.code + ".jpg"} alt={props.code} />
    </div>
    <p style={{ fontWeight: 600, fontSize: "1.8rem" }}>{props.name}</p>
    <p style={{ fontSize: "1.8rem" }}>{props.code}</p>
    <p>
      Credits:{props.credits} Semester:{props.semester}
    </p>
    <div className={styles.border}></div>
  </div>
);

export default Subject;
