import React from "react";
import Modal from "../../Modal/Modal";
import Button from "../../Button/Button";
import styles from "./DeletePopup.module.css";

const DeletePopup = (props) => (
  <Modal closeHandler={props.closeHandler}>
    <h3 className={styles.Heading}>Are you sure?</h3>
    <hr />
    <p>
      You are about to deregister{" "}
      <strong>
        {props.participant.name}, {props.participant.roll}
      </strong>{" "}
      from this course. Do you understand that this action can have serious
      consequences on the mental state of the poor little child? They might
      already be suffering a lot in their personal life and a deregistration
      from a course might lead to them having a breakdown. If you still want to
      cause trouble and panic in the life of a suffering student, go ahead.
    </p>
    <div>
      <Button onClick={props.closeHandler}>Cancel</Button>
      <Button onClick={props.continueHandler}>Continue</Button>
    </div>
  </Modal>
);

export default DeletePopup;
