import React from "react";
import { NavLink } from "react-router-dom";
import StyleSheet from "./NavigationItem.module.css";

const NavigationItem = (props) => (
  <div className={StyleSheet.NavigationItem} onClick={props.onclick}>
    <NavLink
      exact={props.exact}
      to={props.to}
      className={StyleSheet.link}
      activeClassName={StyleSheet.active}
    >
      {props.children}
    </NavLink>
  </div>
);

export default NavigationItem;
